<template>
  <div class="app-container">
    <div class="script-editor-container">
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="16">
          <el-card>
            <div slot="header">
              <span></span>
              <div style="float: right;">
                <el-select v-model="currentScript.language" size="mini" style="width: 120px; margin-right: 10px;">
                  <el-option label="Python" value="python" />
                  <el-option label="Java" value="java" />
                  <el-option label="JavaScript" value="javascript" />
                  <el-option label="C#" value="csharp" />
                </el-select>
                <el-button type="primary" size="mini" @click="saveScript"></el-button>
                <el-button type="success" size="mini" @click="runScript"></el-button>
              </div>
            </div>
            
            <div class="script-editor">
              <codemirror
                ref="codemirror"
                v-model="currentScript.content"
                :options="editorOptions"
                @input="onScriptChange"
              />
            </div>
          </el-card>
        </el-col>
        
        <!--  -->
        <el-col :span="8">
          <el-card>
            <div slot="header">
              <span></span>
            </div>
            
            <el-tabs v-model="activeToolTab" type="border-card">
              <!--  -->
              <el-tab-pane label="" name="elements">
                <div class="tool-section">
                  <el-input
                    v-model="elementSearch"
                    placeholder=""
                    prefix-icon="el-icon-search"
                    size="mini"
                    style="margin-bottom: 10px;"
                  />
                  <div class="element-list">
                    <div
                      v-for="element in filteredElements"
                      :key="element.id"
                      class="element-item"
                      @click="insertElement(element)"
                    >
                      <div class="element-name">{{ element.name }}</div>
                      <div class="element-locator">{{ element.locator }}</div>
                    </div>
                  </div>
                </div>
              </el-tab-pane>
              
              <!--  -->
              <el-tab-pane label="" name="templates">
                <div class="tool-section">
                  <div class="template-category">
                    <h4></h4>
                    <div
                      v-for="template in basicTemplates"
                      :key="template.name"
                      class="template-item"
                      @click="insertTemplate(template)"
                    >
                      <div class="template-name">{{ template.name }}</div>
                      <div class="template-desc">{{ template.description }}</div>
                    </div>
                  </div>
                  
                  <div class="template-category">
                    <h4></h4>
                    <div
                      v-for="template in assertTemplates"
                      :key="template.name"
                      class="template-item"
                      @click="insertTemplate(template)"
                    >
                      <div class="template-name">{{ template.name }}</div>
                      <div class="template-desc">{{ template.description }}</div>
                    </div>
                  </div>
                </div>
              </el-tab-pane>
              
              <!--  -->
              <el-tab-pane label="" name="logs">
                <div class="tool-section">
                  <div class="log-container">
                    <div v-for="(log, index) in executionLogs" :key="index" class="log-item">
                      <span class="log-time">{{ log.timestamp }}</span>
                      <span :class="'log-level-' + log.level">{{ log.level }}</span>
                      <span class="log-message">{{ log.message }}</span>
                    </div>
                  </div>
                </div>
              </el-tab-pane>
            </el-tabs>
          </el-card>
        </el-col>
      </el-row>
      
      <!--  -->
      <el-row style="margin-top: 20px;">
        <el-col :span="24">
          <el-card>
            <div slot="header">
              <span></span>
              <div style="float: right;">
                <el-button type="info" size="mini" @click="clearConsole"></el-button>
              </div>
            </div>
            
            <div class="console-output">
              <div v-for="(output, index) in consoleOutput" :key="index" class="console-line">
                <span class="console-time">{{ output.timestamp }}</span>
                <span :class="'console-type-' + output.type">{{ output.message }}</span>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="scriptManagerVisible" width="80%">
      <div class="script-manager">
        <div class="script-toolbar">
          <el-button type="primary" @click="createNewScript"></el-button>
          <el-button type="success" @click="importScript"></el-button>
          <el-button type="warning" @click="exportScript"></el-button>
        </div>
        
        <el-table :data="scriptList" border style="margin-top: 20px;">
          <el-table-column prop="name" label="" />
          <el-table-column prop="language" label="" width="100" />
          <el-table-column prop="description" label="" />
          <el-table-column prop="created_at" label="" width="150">
            <template slot-scope="scope">
              {{ scope.row.created_at | parseTime }}
            </template>
          </el-table-column>
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-button type="primary" size="mini" @click="loadScript(scope.row)"></el-button>
              <el-button type="info" size="mini" @click="duplicateScript(scope.row)"></el-button>
              <el-button type="danger" size="mini" @click="deleteScript(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { codemirror } from 'vue-codemirror'
import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/material.css'
import 'codemirror/mode/python/python.js'
import pyodideExecutor from '@/utils/pyodide-executor'
import request from '@/utils/request'
import 'codemirror/mode/javascript/javascript.js'
import 'codemirror/mode/clike/clike.js'
import 'codemirror/addon/edit/closebrackets.js'
import 'codemirror/addon/edit/matchbrackets.js'
import 'codemirror/addon/selection/active-line.js'
import 'codemirror/addon/fold/foldcode.js'
import 'codemirror/addon/fold/foldgutter.js'
import 'codemirror/addon/fold/brace-fold.js'
import 'codemirror/addon/fold/foldgutter.css'

export default {
  name: 'UiScript',
  components: {
    codemirror
  },
  data() {
    return {
      activeToolTab: 'elements',
      currentScript: {
        id: null,
        name: '',
        language: 'python',
        content: `# UI
from selenium import webdriver
from selenium.webdriver.common.by import By
from selenium.webdriver.support.ui import WebDriverWait
from selenium.webdriver.support import expected_conditions as EC

class TestScript:
    def __init__(self):
        self.driver = webdriver.Chrome()
        self.wait = WebDriverWait(self.driver, 10)
    
    def test_login(self):
        """"""
        # 
        self.driver.get("https://example.com/login")
        
        # 
        username_input = self.wait.until(
            EC.presence_of_element_located((By.ID, "username"))
        )
        username_input.send_keys("testuser")
        
        # 
        password_input = self.driver.find_element(By.ID, "password")
        password_input.send_keys("testpass")
        
        # 
        login_btn = self.driver.find_element(By.ID, "login-btn")
        login_btn.click()
        
        # 
        success_msg = self.wait.until(
            EC.presence_of_element_located((By.CLASS_NAME, "success-msg"))
        )
        assert "" in success_msg.text
    
    def teardown(self):
        """"""
        self.driver.quit()

if __name__ == "__main__":
    test = TestScript()
    try:
        test.test_login()
        print("")
    except Exception as e:
        print(f": {e}")
    finally:
        test.teardown()
`,
        description: ''
      },
      editorOptions: {
        tabSize: 4,
        mode: 'python',
        theme: 'material',
        lineNumbers: true,
        line: true,
        foldGutter: true,
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        autoCloseBrackets: true,
        matchBrackets: true,
        styleActiveLine: true,
        scrollbarStyle: 'native',
        lineWrapping: false,
        viewportMargin: 10
      },
      elementSearch: '',
      elements: [
        { id: 1, name: '', locator: '#login-btn', page: '' },
        { id: 2, name: '', locator: '#username', page: '' },
        { id: 3, name: '', locator: '#password', page: '' },
        { id: 4, name: '', locator: '.search-btn', page: '' },
        { id: 5, name: '', locator: '.product-list', page: '' }
      ],
      basicTemplates: [
        {
          name: '',
          description: '',
          code: {
            python: 'element = driver.find_element(By.ID, "element-id")\nelement.click()',
            java: 'WebElement element = driver.findElement(By.id("element-id"));\nelement.click();',
            javascript: 'const element = await driver.findElement(By.id("element-id"));\nawait element.click();',
            csharp: 'IWebElement element = driver.FindElement(By.Id("element-id"));\nelement.Click();'
          }
        },
        {
          name: '',
          description: '',
          code: {
            python: 'element = driver.find_element(By.ID, "input-id")\nelement.send_keys("")',
            java: 'WebElement element = driver.findElement(By.id("input-id"));\nelement.sendKeys("");',
            javascript: 'const element = await driver.findElement(By.id("input-id"));\nawait element.sendKeys("");',
            csharp: 'IWebElement element = driver.FindElement(By.Id("input-id"));\nelement.SendKeys("");'
          }
        },
        {
          name: '',
          description: '',
          code: {
            python: 'wait = WebDriverWait(driver, 10)\nelement = wait.until(EC.presence_of_element_located((By.ID, "element-id")))',
            java: 'WebDriverWait wait = new WebDriverWait(driver, 10);\nWebElement element = wait.until(ExpectedConditions.presenceOfElementLocated(By.id("element-id")));',
            javascript: 'const element = await driver.wait(until.elementLocated(By.id("element-id")), 10000);',
            csharp: 'WebDriverWait wait = new WebDriverWait(driver, TimeSpan.FromSeconds(10));\nIWebElement element = wait.Until(ExpectedConditions.ElementIsVisible(By.Id("element-id")));'
          }
        }
      ],
      assertTemplates: [
        {
          name: '',
          description: '',
          code: {
            python: 'element = driver.find_element(By.ID, "element-id")\nassert "" in element.text',
            java: 'WebElement element = driver.findElement(By.id("element-id"));\nAssert.assertTrue(element.getText().contains(""));',
            javascript: 'const element = await driver.findElement(By.id("element-id"));\nconst text = await element.getText();\nassert(text.includes(""));',
            csharp: 'IWebElement element = driver.FindElement(By.Id("element-id"));\nAssert.IsTrue(element.Text.Contains(""));'
          }
        },
        {
          name: '',
          description: '',
          code: {
            python: 'elements = driver.find_elements(By.ID, "element-id")\nassert len(elements) > 0',
            java: 'List<WebElement> elements = driver.findElements(By.id("element-id"));\nAssert.assertTrue(elements.size() > 0);',
            javascript: 'const elements = await driver.findElements(By.id("element-id"));\nassert(elements.length > 0);',
            csharp: 'var elements = driver.FindElements(By.Id("element-id"));\nAssert.IsTrue(elements.Count > 0);'
          }
        }
      ],
      executionLogs: [],
      consoleOutput: [],
      scriptManagerVisible: false,
      scriptList: [
        {
          id: 1,
          name: '',
          language: 'python',
          description: '',
          created_at: new Date('2024-01-15 10:30:00')
        },
        {
          id: 2,
          name: '',
          language: 'java',
          description: '',
          created_at: new Date('2024-01-14 15:20:00')
        }
      ]
    }
  },
  computed: {
    filteredElements() {
      if (!this.elementSearch) {
        return this.elements
      }
      return this.elements.filter(element =>
        element.name.toLowerCase().includes(this.elementSearch.toLowerCase()) ||
        element.locator.toLowerCase().includes(this.elementSearch.toLowerCase())
      )
    }
  },
  watch: {
    'currentScript.language'(newLang) {
      this.updateEditorMode(newLang)
    }
  },
  methods: {
    updateEditorMode(language) {
      const modeMap = {
        python: 'python',
        java: 'text/x-java',
        javascript: 'javascript',
        csharp: 'text/x-csharp'
      }
      this.editorOptions.mode = modeMap[language] || 'python'
    },
    onScriptChange() {
      // 
    },
    insertElement(element) {
      const cursor = this.$refs.codemirror.codemirror.getCursor()
      const locatorCode = this.generateElementCode(element)
      this.$refs.codemirror.codemirror.replaceRange(locatorCode, cursor)
    },
    generateElementCode(element) {
      const language = this.currentScript.language
      const locator = element.locator
      
      const codeMap = {
        python: `driver.find_element(By.CSS_SELECTOR, "${locator}")`,
        java: `driver.findElement(By.cssSelector("${locator}"))`,
        javascript: `await driver.findElement(By.css("${locator}"))`,
        csharp: `driver.FindElement(By.CssSelector("${locator}"))`
      }
      
      return codeMap[language] || codeMap.python
    },
    insertTemplate(template) {
      const cursor = this.$refs.codemirror.codemirror.getCursor()
      const code = template.code[this.currentScript.language] || template.code.python
      this.$refs.codemirror.codemirror.replaceRange(code, cursor)
    },
    saveScript() {
      this.$message({
        message: '',
        type: 'success'
      })
    },
    async runScript() {
      this.executionLogs = []
      this.consoleOutput = []

      this.addLog('INFO', '')
      this.addConsoleOutput('info', '...')

      try {
        // 
        if (!this.currentScript.content.trim()) {
          this.addLog('ERROR', '')
          this.addConsoleOutput('error', '')
          return
        }

        // API
        const response = await request({
          url: '/execute-script',
          method: 'post',
          data: {
            code: this.currentScript.content,
            language: this.currentScript.language
          }
        })

        if (response.data.success) {
          this.addLog('INFO', '')
          this.addConsoleOutput('success', '')

          // 
          if (response.data.output) {
            this.addConsoleOutput('info', ':')
            this.addConsoleOutput('info', response.data.output)
          }
        } else {
          this.addLog('ERROR', '')
          this.addConsoleOutput('error', response.data.error || '')
        }
      } catch (error) {
        // APIPyodide
        if (this.currentScript.language === 'python') {
          this.addLog('WARNING', '')
          this.addConsoleOutput('warning', 'Python...')

          try {
            const result = await pyodideExecutor.execute(this.currentScript.content)

            if (result.success) {
              this.addLog('INFO', '')
              this.addConsoleOutput('success', '')

              if (result.output) {
                this.addConsoleOutput('info', ':')
                this.addConsoleOutput('info', result.output)
              }

              if (result.execution_time) {
                this.addConsoleOutput('info', `: ${result.execution_time.toFixed(2)}ms`)
              }
            } else {
              this.addLog('ERROR', '')
              this.addConsoleOutput('error', result.error)
            }
          } catch (pyodideError) {
            // Pyodide
            this.addLog('WARNING', 'Python')
            this.addConsoleOutput('warning', '')
            this.simulateExecution()
          }
        } else {
          // Python
          this.addLog('WARNING', '')
          this.addConsoleOutput('warning', '')
          this.simulateExecution()
        }
      }
    },

    simulateExecution() {
      // 
      setTimeout(() => {
        this.addLog('INFO', '')
        this.addConsoleOutput('info', '')
      }, 1000)

      setTimeout(() => {
        this.addLog('INFO', '')
        this.addConsoleOutput('info', '')
      }, 2000)

      setTimeout(() => {
        this.addLog('INFO', '')
        this.addConsoleOutput('success', '')
      }, 3000)

      setTimeout(() => {
        this.addLog('INFO', '')
        this.addConsoleOutput('success', '')
      }, 4000)
    },
    addLog(level, message) {
      this.executionLogs.push({
        timestamp: new Date().toLocaleTimeString(),
        level,
        message
      })
    },
    addConsoleOutput(type, message) {
      this.consoleOutput.push({
        timestamp: new Date().toLocaleTimeString(),
        type,
        message
      })
    },
    clearConsole() {
      this.consoleOutput = []
    },
    createNewScript() {
      this.currentScript = {
        id: null,
        name: '',
        language: 'python',
        content: '',
        description: ''
      }
      this.scriptManagerVisible = false
    },
    loadScript(script) {
      this.currentScript = Object.assign({}, script)
      this.scriptManagerVisible = false
    },
    duplicateScript(script) {
      const newScript = Object.assign({}, script)
      newScript.id = null
      newScript.name = script.name + '_copy'
      this.scriptList.push(newScript)
      this.$message.success('')
    },
    deleteScript(script) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        const index = this.scriptList.findIndex(s => s.id === script.id)
        this.scriptList.splice(index, 1)
        this.$message.success('')
      })
    },
    importScript() {
      this.$message({
        message: '...',
        type: 'info'
      })
    },
    exportScript() {
      this.$message({
        message: '...',
        type: 'info'
      })
    }
  },
  mounted() {
    // CodeMirror
    this.$nextTick(() => {
      const codemirrorInstance = this.$refs.codemirror || this.$el.querySelector('.CodeMirror')
      if (codemirrorInstance && codemirrorInstance.CodeMirror) {
        setTimeout(() => {
          codemirrorInstance.CodeMirror.refresh()
        }, 100)
      }
    })
  }
}
</script>

<style scoped>
