  background: #c1c1c1;
  border-radius: 7px;
}

.script-editor >>> .CodeMirror-scroll::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}

.script-editor >>> .CodeMirror-scroll::-webkit-scrollbar-corner {
  background: #f1f1f1;
}

