/**
 * Pyodide Python
 * Python
 */

class PyodideExecutor {
  constructor() {
    this.pyodide = null
    this.isLoading = false
    this.isReady = false
  }

  /**
   * Pyodide
   */
  async init() {
    if (this.isReady) return true
    if (this.isLoading) {
      // 
      while (this.isLoading) {
        await new Promise(resolve => setTimeout(resolve, 100))
      }
      return this.isReady
    }

    try {
      this.isLoading = true
      
      // Pyodide
      const pyodideScript = document.createElement('script')
      pyodideScript.src = 'https://cdn.jsdelivr.net/pyodide/v0.24.1/full/pyodide.js'
      document.head.appendChild(pyodideScript)

      await new Promise((resolve, reject) => {
        pyodideScript.onload = resolve
        pyodideScript.onerror = reject
      })

      // Pyodide
      this.pyodide = await window.loadPyodide({
        indexURL: 'https://cdn.jsdelivr.net/pyodide/v0.24.1/full/'
      })

      // 
      await this.pyodide.loadPackage(['numpy', 'pandas', 'matplotlib'])
      
      this.isReady = true
      this.isLoading = false
      return true
    } catch (error) {
      this.isLoading = false
      console.error('Pyodide:', error)
      return false
    }
  }

  /**
   * Python
   */
  async execute(code) {
    try {
      if (!this.isReady) {
        const initSuccess = await this.init()
        if (!initSuccess) {
          return {
            success: false,
            error: 'Python'
          }
        }
      }

      // 
      const safetyCheck = this.checkCodeSafety(code)
      if (!safetyCheck.safe) {
        return {
          success: false,
          error: safetyCheck.message
        }
      }

      const startTime = performance.now()
      
      // 
      this.pyodide.runPython(`
import sys
from io import StringIO
sys.stdout = StringIO()
sys.stderr = StringIO()
      `)

      // 
      let result
      try {
        result = this.pyodide.runPython(code)
      } catch (error) {
        // 
        const stderr = this.pyodide.runPython('sys.stderr.getvalue()')
        return {
          success: false,
          error: stderr || error.message,
          execution_time: performance.now() - startTime
        }
      }

      // 
      const stdout = this.pyodide.runPython('sys.stdout.getvalue()')
      const stderr = this.pyodide.runPython('sys.stderr.getvalue()')

      // 
      this.pyodide.runPython(`
sys.stdout = sys.__stdout__
sys.stderr = sys.__stderr__
      `)

      return {
        success: true,
        output: stdout,
        error: stderr,
        result: result,
        execution_time: performance.now() - startTime
      }

    } catch (error) {
      return {
        success: false,
        error: error.message
      }
    }
  }

  /**
   * 
   */
  checkCodeSafety(code) {
    const dangerousPatterns = [
      /import\s+os/i,
      /import\s+sys/i,
      /import\s+subprocess/i,
      /open\s*\(/i,
      /file\s*\(/i,
      /exec\s*\(/i,
      /eval\s*\(/i,
      /__import__/i,
      /input\s*\(/i,
      /raw_input\s*\(/i
    ]

    for (const pattern of dangerousPatterns) {
      if (pattern.test(code)) {
        return {
          safe: false,
          message: `: ${pattern.source}`
        }
      }
    }

    return { safe: true }
  }

  /**
   * Python
   */
  async installPackage(packageName) {
    if (!this.isReady) {
      throw new Error('Pyodide')
    }

    try {
      await this.pyodide.loadPackage(packageName)
      return true
    } catch (error) {
      console.error(` ${packageName} :`, error)
      return false
    }
  }

  /**
   * 
   */
  getAvailablePackages() {
    if (!this.isReady) return []
    return this.pyodide.loadedPackages
  }
}

// 
const pyodideExecutor = new PyodideExecutor()

export default pyodideExecutor



